import React,{Component} from 'react';
import './Header.css';
import {Link} from 'react-router-dom';
import {Modal} from 'react-responsive-modal';
import 'react-responsive-modal/styles.css';
import CloseButton from 'react-bootstrap/CloseButton';
import moment from 'moment';




const menuUrl = "https://kinzoinstitute.onrender.com/Categories"
const regUrl = "https://kinzoinstitute.onrender.com/registerNow"


class MenuCategory extends Component {
    constructor(props) {
        super(props);
        console.log(">>> inside MenuCatState", props)

        this.state={
            menuCategory:'',
            adverts: false,
            regForm: false,
            name:'',
            email:'',
            phone:'',
            location:'',
            Message1:'',
            Message2:'',
            Experienced: '',
            date:new Date()
            
        }
    }

    onCloseModalAdverts(){
        this.setState({
            adverts: false
        })
    }

    handleChange = (event) => {
        this.setState({[event.target.name]:event.target.value})
    }

    onOpenModalAdverts(){
        
        this.setState({
            adverts: true
        })
        
        
    }

    onCloseModalReg(){
        this.setState({
            regForm: false
        })
    }

    onOpenModalReg(){
        
        this.setState({
            regForm: true
        })
        
        
    }


    async handleSubmit() {
        
        try {

            var events = {
                email:`${this.state.email}`,
                phone:`${this.state.phone}`,
                Message1:`${this.state.Message1}`,
                Message2: `${this.state.Message2}`,
                Experience: `${this.state.Experienced}`,
                date: moment(this.state.date).format('MMM-DD-YYYY')
               
            }

            let result = await fetch(regUrl, {
                method: 'post',
                
                headers: {
                    'Accept': 'application/json',
                    'Content-type': 'application/json',
                },

                body: JSON.stringify(
                    events
                )
            });
            
            console.log('result>  ' + result)
            alert("Details Submitted Successfully. We will contact you on next steps")
            this.setState({
            name:'',
            email:'',
            phone:'',
            location:'',
            Message1:'',
            Message2:'',
            Experienced: ''
                
            })

            this.onCloseModalReg();
            
            
        } catch(e) {
            console.log(e)
        }
    }

    renderMenu = (data) => {
        
        if (data){
            data.sort((a, b) => a._id - b._id);
            return data.map((item) => {
                return(
                    <>
                       
                        <div className="card addColor">
                            <div className='courseImage'>
                                <img src={item.Productimage} className="card-img-top" alt="menuImages"/>
                            </div>
                            <div className='coursedescription'>
                                <center className = "container">
                                    <p className="card-title setHedd">{item.heading}</p>
                                </center>
                            </div>
                            
                            <div className="card-body mobileViewRel">
                                <center>
                                    <h4 className="card-title mobileViewAbs">{item.product}</h4>
                                </center>
                                
                            </div>
                            <div className="overlay"><br/>
                                <h5 className="note">{item.product}</h5>
                                <hr style={{color:"gold"}}/>
                                <center>
                                    <br/><br/>
                                    <p>
                                        <Link to={`/Courses/${item._id}`} key={item._id} style={{fontFamily:"Georgia, 'Times New Roman', Times, serif", fontSize:"20px", textDecoration:"none", color:"white", fontWeight:"bold"}}><button type="button" className="btn btn-outline-warning" onClick={localStorage.setItem('catID', item._id)}>Explore Course</button></Link>
                                    </p>
                                </center>
                            </div>
                        </div>
                       
                    </>
                            
                )   

            })
        }
        else{
            return(
                <center>
                    <div>
                        <div class="spinner-border text-primary" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-secondary" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-success" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-danger" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                        <div class="spinner-border text-warning" role="status">
                            <span class="visually-hidden">Loading...</span>
                        </div>
                    </div>
                </center>

                
            )
        }   
    }


    render(){
        console.log(">>> Inside menuCatRender", this.state)
        
        return(
            <>
                
                <div className="container putinplace">
                    <div className='fitIn'>
                        <center>
                            <h5 className="text">Program Courses</h5>
                        
                        
                            <div className="row">
                                <div className ="col">
                                    {this.renderMenu(this.state.menuCategory)}
                                </div>

                                
                            
                            </div>
                        </center>
                    
                    </div>

                </div>

                <Modal open={this.state.adverts} center>

                    <div className="advertbgn">
                            <div className="p-3 size3">
                                <CloseButton variant="white" onClick={()=>this.onCloseModalAdverts()}/>
                                
                            </div>
                        
                        <div>
                            <img className="imgsizeCar2" src="https://i.imgur.com/2gD9MbF.png" alt="FirstSlide"/>
                            
                        </div><br/>

                        <center>
                            <h4 className='editAdHeading'> Join the next cohort. Upskill with the best Splunk Academy</h4><br/>
                            <p className='editAdHeading2'> Unleash your fullest potentials and become a data virtuoso. Splunk is the world's leading data analysis and monitoring platform.</p>
                            
                        </center>
                        <br/>
                        <center><button className="btn btn-warning editAdbutton" onClick={()=>{this.onOpenModalReg(); this.onCloseModalAdverts()}}>Register Now</button></center><br/>

                                                 
                       
                    </div>
                    
                </Modal>

                <Modal open={this.state.regForm} onClick={()=>this.onCloseModalReg()} center>

                    <div className="advertbgn22">
                            
                            <div>
                                <center><h4>Registeration form</h4></center><br/>

                                <input type="text" className="form-control mb-3" name="name" require placeholder= "Enter Names" value={this.state.name} onChange={this.handleChange}/>
                                <input type="email" className="form-control mb-3" name="email" require placeholder= "Enter Email" value={this.state.email} onChange={this.handleChange}/>
                                <input type="number" className="form-control mb-3" name="phone" require placeholder= "Phone Number" value={this.state.phone} onChange={this.handleChange}/>
                                <input type="text" className="form-control mb-3" name="location" require placeholder= "City and State" value={this.state.location} onChange={this.handleChange}/>
                                <select className="form-select mb-3" name="Experienced" onChange={this.handleChange}>
                                    <option selected value=''>Do you have an I.T background</option>
                                    <option value="Experienced">Yes</option>
                                    <option value="Experienced">No</option>
                                            
                                </select>
                                <textarea className="form-control mb-3" rows="4" name="Message1" require placeholder= "Why are you interested in taking this program" value={this.state.Message1} onChange={this.handleChange}/>
                                <textarea className="form-control mb-3" rows="4" name="Message2" require placeholder= "Please let us know anything else you will like to share" value={this.state.Message2} onChange={this.handleChange}/>
                                <br/>
                                
                            </div>
                        
                        

                        
                        <br/>
                        <center><button disabled = {this.state.name === '' || this.state.email === '' || this.state.phone === ''}className="btn btn-warning editAdbutton" onClick={()=>this.handleSubmit()}>Send</button></center><br/>
                        <center><button className="btn btn-danger editAdbutton" onClick={()=>(this.onCloseModalReg())}>close</button></center><br/>

                                                 
                       
                    </div>
                    
                </Modal>
                
            
            </>
            
        )
    }

    componentDidMount(){
        console.log(">>> Inside MenuCategoryDidMount", this.state)
        fetch(menuUrl, {method:'GET'})
        .then((res) => res.json())
        .then((data) => {
            this.setState({menuCategory:data})
        })

        setTimeout(() => {
            this.setState({ adverts: true });
        }, 8000);


        
        


    }
}

export default MenuCategory;